  .modal-rate__container {
    padding: 40px 20px;
  }
  .modal-rate__form p {
    font-size: 32px;
  }
  .modal-bad__form {
    grid-template-columns: 1fr;
  }
  .modal-bad__form textarea,
  .modal-bad__form .btn__green,
  .modal-bad__form .consult__check {
    grid-column: auto;
  }
}
@media (max-width: 600px) {
  .consult__flex {
    flex-direction: column;
    gap: 16px;
    margin: 16px 0 0;
    align-items: start;
  }
  .consult__flex .btn__green {
    margin: 0 0 0 auto;
  }
  .about-network__quote {
    grid-template-columns: 1fr;
    gap: 20px;
    position: relative;
  }
  .about-network__quote_text {
    font-size: 16px;
    margin: 0;
  }
  .about-network__quote_col {
    padding: 0;
  }
  .about-network__quote_info {
    position: absolute;
    left: 20px;
    right: 20px;
    bottom: 20px;
    display: none;
  }
  .about-network__quote_info p:nth-child(1) {
    display: none;
  }
  .about-network__quote_info p:nth-child(2) {
    font-size: 16px;
    color: #fff;
  }
  .about-network__quote_info p:nth-child(3) {
    font-size: 15px;
    margin: 5px 0 0;
    color: rgba(255, 255, 255, 0.7);
  }
  .about-network__quote img {
    height: 490px;
    object-position: top;
  }
  .about-network__quote .about-network__qoute_prev {
    position: relative;
  }
  .about-network__quote .about-network__qoute_prev .about-network__quote_info {
    display: block !important;
  }
  .header__stocks {
    grid-template-columns: 1fr;
  }
  .price-service__row {
    grid-template-columns: 1fr;
    gap: 0;
  }
  .price-service__row p {
    text-align: left !important;
  }
  .price-service__row_title {
    display: none;
  }
  .price-service__row_price {
    margin: 10px 0 0;
  }
  .article__content blockquote {
    padding: 70px 20px 20px;
  }
  .article__content blockquote::after {
    height: 40px;
  }
  .header-doc__blockquote {
    display: block;
  }
  .header-doc__blockquote svg {
    display: none;
  }
  .doc-page__wrapper {
    grid-template-columns: 1fr;
  }
  .doc-page__filter {
    grid-template-columns: 1fr;
    gap: 10px;
  }
  .doc-page__filter_input {
    grid-column: auto;
  }
  .contact__wrapper {
    margin: 30px 0 0;
  }
  .contact__info_row {
    justify-content: space-between;
  }
  .contact__info_grid {
    grid-template-columns: 1fr;
  }
  .stocks-page__wrapper {
    grid-template-columns: 1fr;
  }
  .stocks-page .stocks__width {
    grid-column: auto;
  }
  .gallery__row {
    grid-template-columns: 1fr;
  }
}